import { Component } from '@angular/core';
import { Http } from '@angular/http';
import { async } from '@angular/core/testing';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  attemptsLimit;
  initialLimit;
  message;

  constructor(private http: Http) {
    this.attemptsLimit = 5;
    this.initialLimit = 0;
    this.message = "";
  }

  products = [
    {
      item_id: 1,
      image_name: "1.jpg",
      item_name: "Television",
      price: "Rs. 50,000"
    },
    {
      item_id: 2,
      image_name: "2.jpg",
      item_name: "Refrigerator",
      price: "Rs. 70,000"
    },
    {
      item_id: 3,
      image_name: "3.jpg",
      item_name: "Projector",
      price: "Rs. 35,000"
    }
  ];

  orderItem(itemId) {

    let selectedProduct = this.products.filter(product => product.item_id === itemId);
    this.http.post('https://azorderapi.azurewebsites.net/api/order_function?code=TZCp5KMkVSVzpdr8AKXjD3MZO1usYfuOzAzwOas5PxUgWBQQ6/5wPA==', selectedProduct)
      .toPromise()
      .then(response => {
        if (response.status === 200) {
          this.message = response['_body'];
          console.log(response);
        } else {
          this.retryLogic(itemId);
        }

      })
      .catch(e => {
        console.log("Problem in placing the order!");
        this.retryLogic(itemId);
      })
  }

  retryLogic(itemId) {
    console.log('Retrying: ' + this.initialLimit);
    this.message = `We are having problem! Retrying ${this.initialLimit} / ${this.attemptsLimit} times. Please be patient!`;
    if (this.initialLimit < this.attemptsLimit) {
      this.orderItem(itemId);
      this.initialLimit++;
    } else {
      this.message = 'Sorry your server has some serious issues!';
    }
  }

}
